body {
  position: relative;
  margin: 0;
  min-height: 100%;
  min-width: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#leftSpace{
  height: 100%;
  background-color: rgb(255, 255, 255);
  border-left: 2px solid black;
  width:300px;
  margin-left: 25%;
  margin-top: 5%;
  padding: 30px;
  padding-top: 20px;
  padding-bottom: 40px;
  padding-left: 50px;
  padding-bottom: 2%;
}

#photo{
  display: inline-block;
  margin: 10px;
  height: inherit;
  width: 200px;
  float: left;
}

#photoInfo{
  display: inline-block;
  padding-left: 10px;
  padding-top: 10px;
  float:left;
  margin: 10px;
  margin-top: 20px;
  height: inherit;
  width: auto;
}
#tableUsers{
  margin-top:2%;
  margin-left: 30%;
  width:30%;
}
#userInfo{
  margin-left: 25%;
}
#indexText1{
  min-width: 100%;
  margin-top: 5%;
  text-align:left;
  font-family: 'Franklin Gothic Medium';
  font-size: 40px;
  font-display: fallback;
}
#footer { height: 25px; 
   text-align: center;
   color: aliceblue;
   position: absolute; 
   left:0%;
   bottom: 0; 
   margin-bottom: 0;
   width: 100%;
   background-color: rgb(24, 24, 34);
   overflow: hidden;
}
body::after {
  content: '';
  display: block;
  height: 25px;
}
body {
  margin: 0;
}

