}


.dropdown-content {
display: none;
position: absolute;
background-color: #cbe3ff;
z-index: 1;
font-size: small;
min-width: 120px;
box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
padding: 12px 16px;
}
.cwdtable {font-size:14px;color:#333333;width:100%;border-width: 1px;border-color: #729ea5;border-collapse: collapse;}
.cwdtable th {font-size:14px;background:#d7ecef;border-width: 1px;padding: 8px;border-style: solid;border-color: #729ea5;}
.cwdtable tr {background:#ffffff;}
.cwdtable td {font-size:14px;border-width: 1px;padding: 8px;border-style: solid;border-color: #729ea5;}
.cwdtable th,.cwdtable td{text-align:left}
.cwdtable tr:hover {background:#f1f4f4;}
#justDiv{

  height: auto;
}
.heigthMan{
  min-height: 100%;
  min-width:100%;
}
#dropdown:hover .dropdown-content {
display: block;
}
.errorBox{
  margin-top:3px;
  text-align: center;
}
.no_link {
  color:inherit;
  text-decoration: none;
}
.butt{
  overflow:hidden;
  line-height: 1.5;
  text-align:left;
}